export default {
	data() {
		return {
			region:'选择位置',
			form:{
				field02:'0',
				field05:'0',
			},
			list1: [
				{
					value: '0',
					name: '健康',
					checked:true,
				},
				{
					value: '1',
					name: '有发烧咳嗽等症状'
				},
				{
					value: '2',
					name: '无发烧但有咳嗽胸闷等症状'
				},
				{
					value: '3',
					name: '无发烧但有其他症状'
				},
				{
					value:'4',
					name:'其他'
				}
			],
			list2:[
				{
					value:'0',
					name:'有确诊病历'
				},{
					value:'1',
					name:'有长途行程'
				},{
					value:'2',
					name:'在重点疫区'
				}
			]
		};
	},
	onLoad(e) {},
	mounted(){
	},
	methods: {
		handleRadioChange(evt,name){
			this.form[name] = evt.target.value
		},
		send(){
			// 缺少key值 field01 用户id
			let params = {
				...this.form,
				field01:uni.getStorageSync('userId')
			}
			this.$HTTP({
				url:'/healthCollect/save',
				params,
				successCallback: ({data}) => {
					if(data.code == 0){
						uni.showToast({
							title:'提交成功!'
						})
						setTimeout(()=>{
							uni.navigateBack()
						},1500)
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
				}
			})
		}
	}
};
